.home {
  color: #323232;
  text-align: center;
}

.home .content {
  padding-top: 10px;
}

.home .content .features {
  color: white;
  padding: 1.69492%;
  padding-bottom: 0;
}

.home .content .features:after {
  content: " ";
  display: block;
  clear: both;
}

.home .content .features .feature {
  background-color: #39BEC1;
  min-height: 10em;
  margin-bottom: 1.69492%;
  list-style-type: none;
}

@media (min-width: 480px) {
  .home .content .features .feature {
    width: 100%;
    float: left;
    margin-left: 0;
    margin-right: 0;
  }
}

@media (min-width: 720px) {
  .home .content .features .feature {
    width: 49.15254%;
    float: left;
  }
  .home .content .features .feature:nth-child(2n + 1) {
    margin-left: 0;
    margin-right: -100%;
    clear: both;
    margin-left: 0;
  }
  .home .content .features .feature:nth-child(2n + 2) {
    margin-left: 50.84746%;
    margin-right: -100%;
    clear: none;
  }
}

@media (min-width: 1120px) {
  .home .content .features .feature {
    width: 32.20339%;
    float: left;
  }
  .home .content .features .feature:nth-child(3n + 1) {
    margin-left: 0;
    margin-right: -100%;
    clear: both;
    margin-left: 0;
  }
  .home .content .features .feature:nth-child(3n + 2) {
    margin-left: 33.89831%;
    margin-right: -100%;
    clear: none;
  }
  .home .content .features .feature:nth-child(3n + 3) {
    margin-left: 67.79661%;
    margin-right: -100%;
    clear: none;
  }
}

.home .content .features .feature p {
  margin: 10px;
}

.home .content .features .feature .icon {
  fill: #323232;
}



